import { useState, useEffect } from 'react';
import Layout from '../common/Layout';
import { useHistory } from 'react-router-dom';

function Member() {
	const history = useHistory();
	const initVal = {
		userid: '',
		pwd1: '',
		pwd2: '',
		email: '',
		gender: '',
		interests: [],
		edu: '',
		comments: '',
	};
	const [Val, setVal] = useState(initVal);
	const [Err, setErr] = useState({});
	const [Submit, setSubmit] = useState(false);
	const handleChange = (e) => {
		//   input name,value   
		const { name, value } = e.target;
		//  Val State deep copy     name value  State    ( )
		setVal({ ...Val, [name]: value });
	};
	const handleRadio = (e) => {
		const { name, value } = e.target;
		setVal({ ...Val, [name]: value });
	};
	const handleCheck = (e) => {
		const { name } = e.target;
		const inputs = e.target.parentElement.querySelectorAll('input');

		//       true 
		let checkArr = [];
		inputs.forEach((el) => {
			if (el.checked) checkArr.push(el.value);
		});
		setVal({ ...Val, [name]: checkArr });
	};

	const handleSelect = (e) => {
		const { name, value } = e.target;
		setVal({ ...Val, [name]: value });
	};

	const handleSubmit = (e) => {
		e.preventDefault();
		console.log(' ', Val);
		//check         
		//   
		console.log(check(Val));
		setErr(check(Val));
		setSubmit(true);
	};

	const check = (value) => {
		//  State      
		//    
		//    
		const errs = {};
		const eng = /[a-zA-Z]/;
		const num = /[0-9]/;
		const spc = /[~!@#$%^&*()_+]/;
		if (value.userid.length < 5) {
			errs.userid = ' 5  .';
		}
		if (value.pwd1.length < 5 || !eng.test(value.pwd1) || !num.test(value.pwd1) || !spc.test(value.pwd1)) {
			errs.pwd1 = ' 5 , , ,   .';
		}
		if (value.pwd1 !== value.pwd2 || !value.pwd2) {
			errs.pwd2 = '   .';
		}
		if (value.email.length < 8 || !/@/.test(value.email)) {
			errs.email = ' 8  @ .';
		}
		if (!value === '') {
			errs.gender = ' .';
		}
		if (!value.interests.length === 0) {
			errs.interests = '   .';
		}
		if (value.edu === '') {
			errs.edu = ' .';
		}
		if (value.comments.length < 10) {
			errs.comments = '   10  .';
		}
		return errs;
	};

	useEffect(() => {
		const len = Object.keys(Err).length;
		if (len === 0 && Submit) {
			alert('  .');
			history.push('/');
		}
	}, [Err]);

	useEffect(() => {
		console.log(Val);
	}, [Val]);

	return (
		<Layout name={'MEMBER'}>
			<button onClick={() => history.goBack()}> </button>
			<ul className='join'>
				<li>01 </li>
				<li>02 </li>
				<li>03 </li>
			</ul>
			<form onSubmit={handleSubmit}>
				<fieldset>
					<legend className='h'>  </legend>
					<h2> </h2>
					<div className='agreement'>
						<textarea>
							Lorem ipsum dolor sit, amet consectetur adipisicing elit. Quasi suscipit assumenda consectetur mollitia dolores nisi possimus impedit facilis explicabo vitae quae architecto corrupti
							deserunt, aperiam, non minima aliquid molestias vel necessitatibus quidem obcaecati. Sapiente accusamus nobis neque accusantium deleniti culpa, molestias possimus ea dicta vitae
							obcaecati libero nihil cumque modi.
						</textarea>
						<input type='checkbox' id='terms' name='terms' />
						<label for='terms'>   .</label>
					</div>

					<table>
						<tbody>
							{/* user id */}
							<tr>
								<th scope='row'>
									<label htmlFor='userid'>USER ID</label>
								</th>
								<td>
									<input type='text' name='userid' id='userid' placeholder=' ' onChange={handleChange} value={Val.userid} />
									<br />
									{Err.userid && <p>{Err.userid}</p>}
								</td>
							</tr>
							{/* password */}
							<tr>
								<th>
									<label htmlFor='pwd1'>PASSWORD</label>
								</th>
								<td>
									<input type='password' name='pwd1' id='pwd1' placeholder=' ' onChange={handleChange} value={Val.pwd1} />
									<br />
									{Err.pwd1 && <p>{Err.pwd1}</p>}
								</td>
							</tr>
							{/* re password */}
							<tr>
								<th>
									<label htmlFor='pwd2'>RE-PASSWORD</label>
								</th>
								<td>
									<input type='password' name='pwd2' id='pwd2' placeholder=' ' onChange={handleChange} value={Val.pwd2} />
									<br />
									{Err.pwd2 && <p>{Err.pwd2}</p>}
								</td>
							</tr>
							{/* e mail */}
							<tr>
								<th>
									<label htmlFor='email'>E-MAIL</label>
								</th>
								<td>
									<input type='text' name='email' id='email' placeholder=' ' onChange={handleChange} value={Val.email} />
									<br />
									{Err.email && <p>{Err.email}</p>}
								</td>
							</tr>
							{/* gender */}
							<tr>
								<th>GENDER</th>
								<td>
									<label htmlFor='male'>Male</label>
									<input type='radio' name='gender' value='male' id='mail' onChange={handleRadio} />
									<label htmlFor='female'>FeMale</label>
									<input type='radio' name='gender' value='female' id='female' onChange={handleRadio} />
									<br />
									{Err.gender && <p>{Err.gender}</p>}
								</td>
							</tr>
							{/* interest */}
							<tr>
								<th>INTERESTS</th>
								<td>
									<label htmlFor='music'>Music</label>
									<input type='checkbox' name='interests' value='music' id='music' onChange={handleCheck} />
									<label htmlFor='reading'>Reading</label>
									<input type='checkbox' name='interests' value='reading' id='reading' onChange={handleCheck} />
									<label htmlFor='game'>Game</label>
									<input type='checkbox' name='interests' value='game' id='game' onChange={handleCheck} />
									<br />
									{Err.interests && <p>{Err.interests}</p>}
								</td>
							</tr>
							{/* education */}
							<tr>
								<th>
									<label htmlFor='edu'>EDUCATION</label>
								</th>
								<td>
									<select name='edu' id='edu' onChange={handleSelect}>
										<option value=''> .</option>
										<option value='elementary-school'> </option>
										<option value='middle-school'> </option>
										<option value='high-school'> </option>
										<option value='college'> </option>
									</select>
									{Err.edu && <p>{Err.edu}</p>}
								</td>
							</tr>
							{/* comments */}
							<tr>
								<th>
									<label htmlFor='comments'>Leave Message</label>
								</th>
								<td>
									<textarea name='comments' id='comments' cols='30' rows='3' value={Val.comments} onChange={handleChange} placeholder='  .'></textarea>
									<br />
									{Err.comments && <p>{Err.comments}</p>}
								</td>
							</tr>

							{/* btn set */}
							<tr>
								<th colSpan='2'>
									<input type='reset' value='CANCEL' onClick={() => setVal(initVal)} />
									<input type='submit' value='SEND' />
								</th>
							</tr>
						</tbody>
					</table>
				</fieldset>
			</form>
		</Layout>
	);
}
export default Member;
